import { formatDistance } from "date-fns";
import i18n from "@/i18n.js";

import { enUS, zhHK } from "date-fns/locale";
function getLocale() {
  if (i18n.locale === "zhHant") return zhHK;
  else return enUS;
}
export function arriveTimeFilter(input) {
  return formatDistance(new Date(input), new Date(), {
    addSuffix: true,
    locale: getLocale(),
  });
}
