import { actionTypes, mutationTypes } from "./store-types";
import {
  // apiKmbRoute,
  apiKmbRoutes,
  apiKmbStops,
  apiKmbRouteStops,
} from "@/common/api/kmb.transport.api";
const localKeys = {
  // for localStorage
  kmbRoutes: "kmbRoutes",
  kmbStops: "kmbStops",
  cacheStopInfos: "cacheStopInfos",
  bookmarkStops: "bookmarkStops",
};

export const state = {
  kmbRoutes: JSON.parse(localStorage.getItem(localKeys.kmbRoutes)) || {},
  kmbStops: JSON.parse(localStorage.getItem(localKeys.kmbStops)) || {},
  kmbRoute: {},
  kmbRouteStops: [],
  cacheStopInfos:
    JSON.parse(localStorage.getItem(localKeys.cacheStopInfos)) || [],
  bookmarkStops:
    JSON.parse(localStorage.getItem(localKeys.bookmarkStops)) || [],
};

export const mutations = {
  [mutationTypes.SET_KMB_ROUTES](state, payload) {
    state.kmbRoutes = payload;
    localStorage.setItem(localKeys.kmbRoutes, JSON.stringify(state.kmbRoutes));
  },
  [mutationTypes.SET_KMB_STOPS](state, payload) {
    state.kmbStops = payload;
    localStorage.setItem(localKeys.kmbStops, JSON.stringify(state.kmbStops));
  },
  [mutationTypes.SET_KMB_ROUTE](state, payload) {
    state.kmbRoute = payload;
  },
  [mutationTypes.SET_KMB_ROUTE_STOPS](state, payload) {
    state.kmbRouteStops = payload;
  },
  [mutationTypes.PUSH_STOP_TO_CACHE](state, payload) {
    const index = state.cacheStopInfos.findIndex(
      (item) =>
        item.route === payload.route &&
        item.bound === payload.bound &&
        item.serviceType === payload.serviceType
    );
    if (index > -1) {
      state.cacheStopInfos[index] = payload;
    } else state.cacheStopInfos.push(payload);
    localStorage.setItem(
      localKeys.cacheStopInfos,
      JSON.stringify(state.cacheStopInfos)
    );
  },
  [mutationTypes.ADD_BOOKMARK_STOP](state, payload) {
    const index = state.bookmarkStops.find(
      (item) =>
        item.stopInfo.bound === payload.stopInfo.bound &&
        item.stopInfo.stop === payload.stopInfo.stop &&
        item.stopInfo.service_type === payload.stopInfo.service_type &&
        item.stopInfo.route === payload.stopInfo.route
    );
    if (index > -1) {
      state.bookmarkStops[index] = payload;
    } else {
      state.bookmarkStops.push(payload);
    }
    localStorage.setItem(
      localKeys.bookmarkStops,
      JSON.stringify(state.bookmarkStops)
    );
  },
  [mutationTypes.REMOVE_BOOKMARK_STOP](state, payload) {
    const index = state.bookmarkStops.findIndex(
      (item) =>
        item.stopInfo.bound === payload.bound &&
        item.stopInfo.stop === payload.stop &&
        item.stopInfo.service_type === payload.serviceType &&
        item.stopInfo.route === payload.route
    );
    if (index > -1) {
      state.bookmarkStops.splice(index, 1);
    }
    localStorage.setItem(
      localKeys.bookmarkStops,
      JSON.stringify(state.bookmarkStops)
    );
  },
};

export const actions = {
  async [actionTypes.fetchKmbRoutes](context, payload) {
    const data = await apiKmbRoutes();
    context.commit(mutationTypes.SET_KMB_ROUTES, data);
  },
  async [actionTypes.fetchKmbRoute](context, payload) {
    const { route, bound, serviceType } = payload;
    if (context.getters.kmbRoutesData.length === 0) {
      await context.dispatch(actionTypes.fetchKmbRoutes);
    }
    const data = context.getters.kmbRoutesData.find(
      (item) =>
        item.route === route &&
        item.bound === bound &&
        item.service_type === serviceType
    );
    // bound = bound === "I" ? "inbound" : bound === "O" ? "outbound" : bound;
    // const data = await apiKmbRoute(route, bound, serviceType);
    context.commit(mutationTypes.SET_KMB_ROUTE, data);
  },
  async [actionTypes.fetchKmbStops](context, payload) {
    const data = await apiKmbStops();
    context.commit(mutationTypes.SET_KMB_STOPS, data);
  },
  async [actionTypes.fetchKmbRouteStops](context, payload) {
    let { route, bound, serviceType } = payload;
    bound = bound === "I" ? "inbound" : bound === "O" ? "outbound" : bound;
    const data = await apiKmbRouteStops(route, bound, serviceType);
    context.commit(mutationTypes.SET_KMB_ROUTE_STOPS, data);
  },
  async [actionTypes.genKmbRouteStopsInfoCache](context, payload) {
    const { route, bound, serviceType } = payload;

    // check cache have this route stop info
    const routeStopsCache = context.getters.getStopsInfo(
      route,
      bound,
      serviceType
    );
    if (routeStopsCache) return; // have cache , quit function now

    // get stops of the route
    await context.dispatch(actionTypes.fetchKmbRouteStops, {
      route,
      bound,
      serviceType,
    });
    // if don't have stop info data, fetch it again
    if (context.getters.kmbStopsData.length === 0) {
      await context.dispatch(actionTypes.fetchKmbStops);
    }
    // mapping the stops of the route(kmbRouteStops) with stop info(kmbStops)
    const stopsWithInfo = context.state.kmbRouteStops.data.map((routeStop) => {
      const stopInfo = context.state.kmbStops.data.find(
        (stop) => stop.stop === routeStop.stop
      );
      return {
        ...routeStop,
        ...stopInfo,
      };
    });
    // add the Stops of  with info to the cache
    const routeStopsWithInfo = {
      route,
      bound,
      serviceType,
      stops: stopsWithInfo,
    };
    context.commit(mutationTypes.PUSH_STOP_TO_CACHE, routeStopsWithInfo);
  },
  async [actionTypes.addBookmarkStop](context, payload) {
    // add bookmark
    const { route, bound, serviceType, stop } = payload;

    // get route info for cache
    if (context.getters.kmbRoutesData?.length === 0) {
      await this.$store.dispatch(actionTypes.fetchKmbRoutes);
    }
    const routeInfo = context.getters.kmbRoutesData.find(
      (item) =>
        route === item.route &&
        bound === item.bound &&
        serviceType === item.service_type
    );
    // get stop info for cache
    await context.dispatch(actionTypes.genKmbRouteStopsInfoCache, {
      route,
      bound,
      serviceType,
    });
    const stopInfo = context.getters
      .getStopsInfo(route, bound, serviceType)
      .find((item) => item.stop === stop);

    const bookmark = {
      routeInfo,
      stopInfo,
    };

    context.commit(mutationTypes.ADD_BOOKMARK_STOP, bookmark);
  },
  [actionTypes.removeBookmarkStop](context, payload) {
    context.commit(mutationTypes.REMOVE_BOOKMARK_STOP, payload);
  },
};

const getters = {
  kmbRoutesData(state) {
    return state.kmbRoutes.data || [];
  },
  kmbRouteData(state) {
    return state.kmbRoute || {};
  },
  kmbStopsData(state) {
    return state.kmbStops.data || [];
  },
  kmbRouteStopsData(state) {
    return state.kmbRouteStops.data || [];
  },
  getStopsInfo: (state) => (route, bound, serviceType) => {
    return state.cacheStopInfos.find(
      (item) =>
        item.route === route &&
        item.bound === bound &&
        item.serviceType === serviceType
    )?.stops;
  },
  bookmarkStops(state) {
    return state.bookmarkStops || [];
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
