<template>
  <v-bottom-navigation :value="value" color="primary" horizontal app fixed>
    <template v-for="(item, index) in menu">
      <v-btn :key="index" @click="clickItem(item)">
        <span>{{ item.label }}</span>

        <v-icon>{{ item.icon }}</v-icon>
      </v-btn>
    </template>
  </v-bottom-navigation>
</template>

<script>
export default {
  name: "MenuTabbar",
  props: {
    menu: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    value() {
      const index = this.menu.findIndex(
        (x) => x.path === this.$router.currentRoute.path
      );
      return index;
    },
  },
  data() {
    return {};
  },
  methods: {
    clickItem(item) {
      this.$emit("click", item);
    },
  },
};
</script>

