<template>
  <v-app class="gp-full-height">
    <v-app-bar app>
      <!-- open menu drawer button , hide when screen on xs (<600px)-->
      <v-app-bar-nav-icon
        class="d-none d-sm-flex"
        @click="drawer = !drawer"
      ></v-app-bar-nav-icon>

      <v-toolbar-title>{{ $t("common.home") }} </v-toolbar-title>
      <v-spacer></v-spacer>
      <PWAPrompt />
    </v-app-bar>

    <!--Drawer-->
    <MenuDrawer
      @click="menuClick"
      class="d-none d-sm-flex"
      :menu="menu"
      v-model="drawer"
    />

    <v-main>
      <router-view />
    </v-main>
    <MenuTabbar class="d-flex d-sm-none" @click="menuClick" :menu="menu" />
  </v-app>
</template>

<script>
import PWAPrompt from "@/components/PWAPrompt.vue";
import MenuDrawer from "@/components/MenuDrawer.vue";
import MenuTabbar from "@/components/MenuTabbar.vue";
import debounce from "lodash/debounce";
export default {
  name: "App",
  components: {
    MenuDrawer,
    MenuTabbar,
    PWAPrompt,
  },
  data: () => ({
    drawer: false,
  }),
  computed: {
    menu() {
      return [
        {
          label: this.$t("common.home"),
          path: "/",
          icon: "mdi-home",
        },
        {
          label: this.$t("transport.routes"),
          path: "/routes",
          icon: "mdi-bus",
        },
        {
          label: this.$t("common.setting"),
          path: "/setting",
          icon: "mdi-cog",
        },
      ];
    },
  },
  methods: {
    menuClick(item) {
      this.$router.push(item.path).catch(() => {}); // need catch error and do nothing for solving the redundant navigation
    },
    addPwa() {
      // if (states.deferredPrompt) {
      //     states.deferredPrompt.prompt();
      //     states.deferredPrompt = null;
      //   }
    },
    setViewHeight() {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty("--vh", `${vh}px`);
    },
  },
  mounted() {
    this.setViewHeight();

    const debouncedSetHeight = debounce(this.setViewHeight, 50);

    window.addEventListener("resize", debouncedSetHeight);

    this.$once("destroyed", () => {
      window.removeEventListener("resize", debouncedSetHeight);
    });
  },
};
</script>

<style scoped lang="scss">
/**
    Overwrite vuetify's default v-application--wrap min-height: 100vh;
    Due to the many problems with vh on mobile devices.
   */
.gp-full-height ::v-deep .v-application--wrap {
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
}
</style>
