import axios from "axios";
class ApiTool {
  constructor(API_URL) {
    this.axios = axios.create({
      baseURL: API_URL,
    });
  }

  get(url, params) {
    return new Promise((resolve, reject) => {
      this.axios
        .get(url, {
          params: params,
        })
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  post(url, params) {
    return new Promise((resolve, reject) => {
      this.axios
        .post(url, params)
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}

export default ApiTool;
