<template>
  <v-navigation-drawer v-model="drawer" fixed temporary app>
    <v-sheet color="grey lighten-4" class="pa-4">
      <v-avatar class="mb-4" color="grey darken-1" size="64"></v-avatar>

      <div>Testing</div>
    </v-sheet>

    <v-divider></v-divider>

    <v-list nav>
      <v-list-item
        @click="clickItem(item)"
        v-for="(item, index) in menu"
        :key="index"
        link
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <!-- <router-link :to="item.path"> -->
          <v-list-item-title>{{ item.label }}</v-list-item-title>
          <!-- </router-link> -->
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: "MenuDrawer",
  props: {
    value: {
      type: Boolean,
      default: true,
    },
    menu: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    drawer: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
  data() {
    return {};
  },
  methods: {
    clickItem(item) {
      this.$emit("click", item);
    },
  },
};
</script>

