import { actionTypes, mutationTypes } from "./store-types";
import i18n, { storeLangKey } from "@/i18n";

export const state = {
  lang: localStorage.getItem(storeLangKey) || "zhHant",
};

export const mutations = {
  [mutationTypes.SET_LANG](state, payload) {
    i18n.locale = payload;
    localStorage.setItem(storeLangKey, payload);
  },
};

export const actions = {
  [actionTypes.setLang](context, payload) {
    context.commit(mutationTypes.SET_LANG, payload);
  },
};

export default {
  state,
  actions,
  mutations,
};
