import ApiTool from "./apiTool";
const kmbRequest = new ApiTool("https://data.etabus.gov.hk/v1/transport/kmb/");

// get all routes data
export const apiKmbRoutes = () => kmbRequest.get("/route");
// get a routes data
export const apiKmbRoute = (route, direction, serviceType) =>
  kmbRequest.get(`/route/${route}/${direction}/${serviceType}`);

// get all stops data
export const apiKmbStops = () => kmbRequest.get("/stop");
// get a stop
export const apiKmbStop = (stopId) => kmbRequest.get(`/stop/${stopId}`);

// get the stops of route
export const apiKmbRouteStops = (route, direction, serviceType) =>
  kmbRequest.get(`/route-stop/${route}/${direction}/${serviceType}`);

// get eta
export const apiKmbEta = (stopId, route, serviceType) =>
  kmbRequest.get(`/eta/${stopId}/${route}/${serviceType}`);
