import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: () => import("../views/HomeView.vue"),
  },
  {
    path: "/about",
    name: "about",
    component: () => import("../views/AboutView.vue"),
  },
  {
    path: "/setting",
    name: "setting",
    component: () => import("../views/SettingView.vue"),
  },
  {
    path: "/routes",
    name: "routes",
    component: () => import("../views/RoutesView.vue"),
  },
  {
    path: "/routeStop/",
    name: "routeStop",
    component: () => import("../views/RouteStopView.vue"),
  },
  {
    path: "/routeStop/:id/:name/:service_type",
    name: "routeStop2",
    component: () => import("../views/RouteStopView.vue"),
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

export default router;
